body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#root {
    height: 100%;
}
#root .trigger {
    font-size: 18px;
    line-height: 64px;
    padding: 0 24px;
    cursor: pointer;
    -webkit-transition: color .3s;
    transition: color .3s;
}

#root .trigger:hover {
    color: #1890ff;
}

#root .logo {
    height: 32px;
    /*background: rgba(255,255,255,.2);*/
    margin: 16px;
    text-align: left;
    color: white;
    font-size: 24px;
    overflow: hidden;
}
.code_key {
  font-weight: bold;
  color: #c41d7f; }

.form_item_4 .ant-form-item {
  margin-bottom: 2px; }
  .form_item_4 .ant-form-item input {
    border: 1px dotted #ddd; }
  .form_item_4 .ant-form-item textarea {
    border: 1px dotted #ddd; }

.form_row_small_margin .ant-form-item {
  margin-bottom: 2px; }

.form_row_small_margin .ant-col-10 {
  width: 41.66666667% !important; }

.form_row_small_margin .ant-form-text {
  display: inline-block;
  padding-left: 10px; }

.validate-unit {
  display: inline-block;
  padding-right: 18px;
  margin-bottom: 5px; }
  .validate-unit input {
    height: 16px;
    margin-right: 3px;
    border: 1px solid black !important; }

label.active {
  background-color: #1890ff;
  color: white; }

.fun-return-vo {
  display: inline-block;
  min-width: 40px;
  border: 1px dotted #ddd; }

.table-columns-tab td, .table-columns-tab th {
  padding: 1px !important; }

.database-list .ant-list-item {
  padding: 1px 0;
  color: white; }
  .database-list .ant-list-item .ant-list-item-content {
    display: block; }

