@import '~antd/dist/antd.css';
@import '~ztree/css/zTreeStyle/zTreeStyle.css';

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#root {
    height: 100%;
}
#root .trigger {
    font-size: 18px;
    line-height: 64px;
    padding: 0 24px;
    cursor: pointer;
    transition: color .3s;
}

#root .trigger:hover {
    color: #1890ff;
}

#root .logo {
    height: 32px;
    /*background: rgba(255,255,255,.2);*/
    margin: 16px;
    text-align: left;
    color: white;
    font-size: 24px;
    overflow: hidden;
}